<template>
  <div>
    <el-row class="art-search">
      <el-col :span="6">
        <el-input v-model="searchVal" placeholder="请搜索内容"></el-input>
      </el-col>
      <el-col :span="18">
        <el-button @click="handleSearch" class="art-button" type="primary"
          icon="el-icon-search">搜索</el-button>
      </el-col>
    </el-row>
    <el-table
       v-loading="loading"
      ref="multipleTable"
      :data="articleList"
      tooltip-effect="dark"
      style="width: 100%"
      @selection-change="handleSelectionChange"
    >
      <el-table-column type="selection" width="55"></el-table-column>
      <el-table-column label="标题" width="420">
        <template slot-scope="scope">
          <router-link class="urlA" :to="'/editblog?bid=' + scope.row.id">
            {{ scope.row.title }}</router-link>
        </template>
      </el-table-column>
      <el-table-column prop="tags" label="标签" width="120"></el-table-column>
      <el-table-column prop="utime" label="修改日期" width="120"></el-table-column>
      <el-table-column prop="ctime" label="创建日期" show-overflow-tooltip></el-table-column>
      <el-table-column label="操作">
        <template slot-scope="scope">
          <el-button size="mini" @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
          <el-button size="mini" type="danger"
             @click="handleDelete(scope.$index, scope.row)">删除</el-button>
        </template>
      </el-table-column>
    </el-table>

    <el-pagination
      background
      layout="prev, pager, next"
      :total="total"
      :page-size="pageSize"
      :pager-count="limit"
      @current-change="changePage"
    ></el-pagination>
  </div>
</template>

<script>
import { getArticle } from '../api';

export default {
  data() {
    return {
      loading: false,
      searchVal: null,
      total: 0, // 总记录数
      curPage: 1, // 当前页
      limit: 9, // 数字按钮数
      pageSize: 7, // 每页记录数
      articleList: [],
      multipleSelection: [],
    };
  },
  methods: {
    toggleSelection(rows) {
      if (rows) {
        rows.forEach((row) => {
          this.$refs.multipleTable.toggleRowSelection(row);
        });
      } else {
        this.$refs.multipleTable.clearSelection();
      }
    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    async getData(page, pageSize) {
      this.loading = true;
      // condition  { tid: Array, content: String }
      const condition = {};
      // if (this.tagId) condition.tid = this.tagId;
      if (this.searchVal) condition.content = this.searchVal;
      //
      const result = await getArticle(page, pageSize, condition);
      this.articleList = result.data; // JSON.parse(result.data);
      this.total = +result.count;
      this.curPage = +page;
      this.loading = false;
    },
    handleEdit(index, row) {
      console.log(index, row);
    },
    handleDelete(index, row) {
      console.log(index, row);
    },
    changePage(page) {
      this.getData(page, this.pageSize);
    },
    handleSearch() {
      this.getData(this.curPage, this.pageSize);
    },
  },
  async created() {
    await this.getData(this.curPage, this.pageSize);
  },
};
</script>

<style lang="scss" scoped>
.art-search {
  height: 60px;
  line-height: 60px;
  margin-left: 10px;
  .art-button {
    margin-left: 10px;
  }

}
.urlA{
  font-size: 12px;
    color:#13102b;
    text-decoration: none;
  }
</style>
